.account-block {
    padding: 0;
    background-image: url(https://image.shutterstock.com/image-photo/multiethnic-group-students-stdying-together-260nw-1048526521.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    position: relative;
}
.account-block .overlay {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
}
.account-block .account-testimonial {
    text-align: center;
    color: #fff;
    position: absolute;
    margin: 0 auto;
    padding: 0 1.75rem;
    bottom: 3rem;
    left: 0;
    right: 0;
}
.text-theme {
    color: #989baa !important;
}
.btn-theme {
    background-color: #5369f8;
    border-color: #5369f8;
    border: 8px;
    color: rgb(255, 255, 255);
}